.wp-block-create-block-gutenpride {
	background-color: #21759b;
	padding: 2px;
}
.banner {
  display: flex;
  width: 100%;
  background-color: #00a;
}

.nav-primary {
  display: flex;
  width: 80%;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}

.nav-primary a {
  color: white;
}

.menu-menu-container {
  display: flex;
  width: 30%;
  justify-content: space-around;
}

.menu-menu-container ul {
  display: flex;
  width: 100%;
  justify-content: space-around;
  list-style-type: none;
}

.menu-menu-container ul li a {
  display: block;
}

.banner__desc {
  width: 100%;
  height: 700px;
  background-image: url('../../../../themes/sage/public/images/banner.png');
  background-repeat: no-repeat;
  background-size: cover;
}

.banner__desc div {
  width: 50%;
  position: absolute;
  top: 30%;
  left: 10%;
}

.banner__desc h1 {
  font-size: 3rem;
  text-transform: uppercase;
}

.banner__desc h2 {
  font-size: 2rem;
}

.banner__desc p {
  font-size: 2rem;
}

.banner__desc a {
  padding: 10px;
  color: white;
  background-color: blueviolet;
  text-transform: uppercase;
  border-radius: 2px;
}